import s from './Post.module.css'

type PostPropsType = {
    id: number
    message: string
    likesCount: number
}

const Post: React.FC<PostPropsType> = (props) => {
    return (
        <div className={s.posts}>
            <div className={s.item}>
                <img
                    src='data:image/jpeg;base64,'/>
                {props.message}
                <div>
                    <span className={s.like}>like {props.likesCount}</span>
                </div>
            </div>
        </div>
    )
};
export default Post